.container {
    width: 100%;
    height: 110px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
}

.header {
    font-weight: normal;
    margin: 0;
    font-size: 20px;
    text-transform: uppercase;
}

.number {
    font-size: 16px;
    margin-right: 8px;
}

.logoCont {
    width: 85px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

